import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home'
import Login from '@/views/Login'
import Hello from '@/components/Hello'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      children: [
        {
          path: 'user',
          name: 'user',
          component: Hello
        },
        {
          path: 'login',
          name: 'login',
          component: Login
        }
      ]
    }
  ]
})



